import { onScroll, elemIsInView } from '../tools/utilities/helper-functions';

const SELECTOR_IMAGES = '[data-image]';
const animationImages = document.querySelectorAll<HTMLElement>(SELECTOR_IMAGES);

animationImages.forEach((image) => {
  if (elemIsInView(image)) {
    image.classList.add('slide-in');
  }
});

onScroll(() => {
  animationImages.forEach((image) => {
    if (elemIsInView(image)) {
      image.classList.add('slide-in');
    }
  });
});
